          </div>
          <el-upload v-show="showUnload"
            class="avatar-uploader"
            :class="{disabled:uploadDisabled}"
            :action="uploadUrl"
            :data="imgdata"
            list-type="picture-card"
            :multiple='false'
            :file-list="fileList"
            name="image"
            :beforeUpload="beforeUpload"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :on-change="handleChange"
            :on-success="handleSuccess"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>

          <!--<el-button size="small" type="primary" @click="saveFile(formImg)">点击上传文件</el-button>-->
        </el-form-item>
        <!--<el-form-item label="字体" prop="dest_version">
          <el-select v-model="formAdd.font" class="diainp" placeholder="请选择" @change="changeFont">
            <el-option
              v-for="item in fontse"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="字号" prop="font_size">
          <el-input class="diainp" type="text" v-model="formAdd.font_size"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.name.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
