            <el-input class="diainp2" type="number" v-model="formAdd.name.font_size"></el-input>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input class="diainp2" type="number" v-model="formAdd.name.x"></el-input>
            <span class="inp-span">Y</span>
