        <el-form-item label="证书编号" prop="serial_number">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.serial_number.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input class="diainp2" type="number" v-model="formAdd.serial_number.font_size"></el-input>
          </div>
