            <el-input class="diainp2" type="number" v-model="formAdd.serial_number.y"></el-input>
          </div>
        </el-form-item>
        <el-form-item label="发证日期" prop="image_design">
          <div class="small-form-item">
            <span class="inp-span">字体</span>
            <el-select v-model="formAdd.image_design.date.font" class="diainp2" placeholder="请选择" @change="changeFont">
              <el-option
                v-for="item in fontse"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <span class="inp-span">字号</span>
            <el-input class="diainp2" type="number" v-model="formAdd.image_design.date.font_size"></el-input>
          </div>
          <div>
            <span class="inp-span">X</span>
            <el-input class="diainp2" type="number" v-model="formAdd.image_design.date.x"></el-input>
            <span class="inp-span">Y</span>
            <el-input class="diainp2" type="number" v-model="formAdd.image_design.date.y"></el-input>
          </div>
        </el-form-item>-->
        <el-form-item>
          <el-button type="primary" :disabled="formAdd.img_name ==''" @click="saveForm('formAdd')">保存</el-button>
          <!--<el-button type="primary" :disabled="formAdd.img_name ==''" @click="savePreview('formAdd')">预览</el-button>-->
        </el-form-item>
      </el-form>
    </div>
    <div class="img-cont">
      <a class="imgbig">
        <img :src="testUrl" alt="">
      </a>
    </div>

  </div>
</template>

<script>
  import Qs from 'qs'
  import store from '../store'
  export default {
    data () {
      return {
        certs:[],
        curImgUrl:'',
        showUnload:true,
        sqStatus:['开放','关闭'],
        fontse:[],
        formAdd: {
          cert_id:'',
          cert_name: '',
          authority:'',
          price:'0',
          status:'enabled',
          img_name:'',
          name:{
            font:'',
            font_size:12,
            x:0,
            y:0
          },
          english_name:{
            font:'ddd',
            font_size:12,
            x:0,
            y:0
          },
          personal_id:{
            font:'ddd',
            font_size:12,
            x:0,
            y:0
          },
          serial_number:{
            font:'ddd',
            font_size:12,
            x:0,
            y:0
          },
          image_design:{
            date:{
              font:'ddd',
              font_size:12,
              x:0,
              y:0
            }
          }
        },
        rulesAdd: {
          cert_name: [
            {required: true, message: '请输入证书名称', trigger: 'blur'},
            // {validator: this.validateMac, trigger: 'blur'}
          ],
          authority: [
            {required: true, message: '请输入颁发机构', trigger: 'blur'}
          ],
          price: [
            {required: true, message: '请输入证书价格', trigger: 'blur'}
          ],
          font_sizeeee: [
            {required: true, message: '请输入字号', trigger: 'blur'}
          ]
        },

        formImg:{

        },
        fileList:[],
        imageUrl:'',
        uploadUrl:this.baseUrl+'/api/v1/admin/images',
        dialogImageUrl: '',
        dialogVisible: false,
        getImgUrl:'',
        testUrl:'',

        imgdata:{
          'Authorization':'Bearer '+ localStorage.token
        }

      }
    },
    computed: {
      uploadDisabled:function() {
        return this.fileList.length >0
      },

    },
    mounted(){
      store.dispatch('loadSiderbarData');
    },
    created: function(){
      this.getFont();
      this.getCerList();
    },
    methods:{
      getCerList: function(){
        let self = this;
        self.$axios({
          type:'get',
          url:self.baseUrl+'/api/v1/admin/certs'
        }).then(res =>{
          //console.log(res.data);
          self.certs = res.data.data;


        })



      },
      getFont: function(){
        let self = this;
        self.$axios.get(self.baseUrl+'/api/v1/admin/fonts').then(function(res){
          if(res.data.success){
            self.formAdd.fonts = res.data.data;
          }else{
            self.$message.error(res.data.msg);
          }
        })
      },
      changeFont:function(){

      },
      clickImg(){
        let self = this;
        self.curImgUrl = '';
        self.showUnload = true;


      },
      savePreview: function(formName){
        const self = this;
        self.$refs[formName].validate(function(valid){
          if (valid) {
            let param = {
              img_name : self.formAdd.img_name
            }
            let params = Qs.stringify(param);
            self.$axios.get(self.baseUrl + '/api/v1/admin/cert/preview',{
              params:{
                img_name:self.formAdd.img_name
              }
            }).then(function(res){
              if(res.data.success){
                self.testUrl = res.data.data.imageURL;
                //self.$message({message:'图片生成成功',type:'success'});
              }else{
                self.$message.error(res.data.msg);
              }

            },function(err){
              console.log(err);
            })


          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveForm: function(formName){
        const self = this;
        self.$refs[formName].validate(function(valid){
          if (valid) {
            let params = Qs.stringify(self.formAdd);
            // self.$axios({
            //   method: "POST",
            //   url: self.baseUrl + '/api/v1/admin/cert/add',
            //   data:paramstest,
            //   transformRequest: [function (data) {
            //     // Do whatever you want to transform the data
            //     let ret = ''
            //     for (let it in paramstest) {
            //       // 如果要发送中文 编码
            //       ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            //     }
            //     return ret
            //   }],
            //   headers: {
            //     'Content-Type': 'application/x-www-form-urlencoded',  //指定消息格式
            //   },
            // }).then(function(res){
            //   console.log(res);
            // })
            self.$axios.put(self.baseUrl + '/api/v1/admin/certs',params).then(function(res){
              if(res.data.success){
                self.$message({message:'操作成功',type:'success'});
                store.dispatch('loadSiderbarData')
              }else{
                self.$message.error(res.data.msg);
              }

            },function(err){
              self.$message.error(err.msg);
            })


          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      changeCert(val){
        var self = this;
        var arr = self.certs;
        for(let i in arr){
          if(arr[i].cert_name == val){
            self.formAdd.authority = arr[i].authority;
            self.formAdd.price = arr[i].price;
            self.curImgUrl = self.baseUrl + '/api/' + arr[i].image_design.img_name;
            self.formAdd.img_name = arr[i].image_design.img_name
            self.showUnload = false;
            self.formAdd.cert_id = arr[i].cert_id;
          }
        }

      },
      changeStatus: function(){

      },
      handleRemove(file, fileList) {
        //console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        // this.dialogVisible = true;
      },
      handleChange: function(file,fileList){

      },
      handleSuccess: function(response,file,fileList){
        let self = this;
        if(response.success){
          // self.getImgUrl = response.data.data.imageURL;
          self.formAdd.img_name = response.data.imageSavePath;
          self.testUrl = response.data.imageURL;
          self.$message({message:'图片上传成功',type:'success'});
        }else{
          self.$message.error(response.data.msg)
        }

      },
      beforeUpload: function(file,fileName){
        const isJPG = file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 png 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }

        // let fd = new FormData()
        // fd.append('image', file, fileName)
        // this.$axios.post(this.uploadUrl, fd,{
        //
        //   headers: {
        //     'Authorization': 'Bearer '+localStorage.token
        //   }
        // })

        return isJPG && isLt2M;
      },


    }

  }
</script>
<style scoped>
  .curimg-cont{
    display: inline-block;
    width:148px;
    height:148px;
  }
  .curimg-cont img{
    width:100%;
    height:100%;
  }
  .diainp{width:274px;}
  .diainp2{width:80px;}
  .inp-span{display:inline-block;width:28px;margin:0 5px 0 18px;text-align: right;color:#999;}
  .small-form-item{margin-bottom:8px;}
  .form-box2{float:left;width:45%;}
  .img-cont{float:left;width:54%;text-align: center;}
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 60px;
    color: #8c939d;
    font-weight: bold;
    width: 218px;
    height: 218px;
    line-height: 218px;
    text-align: center;
    border-radius: 4px;
    border:1px dashed #dcdfe6;
  }
  .avatar {
    width: 218px;
    height: 218px;
    display: block;
  }

</style>
<style>
  .disabled .el-upload--picture-card {
    display: none;
  }
</style>



